* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: linear-gradient(blue, yellow);
  background-repeat: no-repeat;
  line-height: 1.5;
  height: 100vh;
  padding-left: 50px;
}

.wrapper {
  display: block;
}

#search-box {
  font: inherit;
  padding: 8px;
  min-width: 235px;
  margin-bottom: 16px;

  transition: border-color 300ms ease-in-out, background-color 300ms ease-in-out;
}

#search-box:focus {
  outline: transparent;
  border-color: blueviolet;
  background-color: rgb(216, 184, 245);
}

#search-box:focus::placeholder {
  color: transparent;
}

.country-list,
.country-info {
  list-style: none;
}

.country-name {
  font-size: 35px;
}

/* -------for weather------------ */
.weather-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
}

.weather-card {
  text-align: center;
  padding: 10px;
  border: 1px skyblue solid;
  border-radius: 4px;
  box-shadow: 0 1px 1px #0000001f, 0 4px 4px #0000000f, 1px 4px 6px #00000029,
    0 -1px 1px #0000001f, 0 -4px 4px #0000000f, -1px -4px 6px #00000029;
}

.weather-location {
  font-size: 20px;
  margin: 0;
}
